import { getColors } from './colors';

type DesignSystemGradients = {
  aiBorderGradient: string;
};

export function getGradients(isDarkMode: boolean): DesignSystemGradients {
  const colors = getColors(isDarkMode);
  return {
    aiBorderGradient: `linear-gradient(135deg, ${colors.branded.ai.gradientStart} 21.06%, ${colors.branded.ai.gradientMid} 53.65%, ${colors.branded.ai.gradientEnd} 80.06%)`,
  };
}
